.card {
    width: 100%;
    height: 17rem;
    font-size: 6rem;
    background-color: #2e3d49;
    transition: all 1s ease;
    border-radius: 1rem;
    box-shadow: 0 .5rem .5rem .5rem rgba(0, 0, 0, .5);

    @media only screen and (max-width: 900px) {
        height: 13rem;
    }

    @media only screen and (max-width: 900px) {
        height: 10rem;
    }

    @media only screen and (max-width: 500px) {
        height: 7rem;
    }

    //Individual card grid position
    &-1 {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
    }

    &-2 {
        grid-column: 3 / 4;
        grid-row: 1 / 2;
    }

    &-3 {
        grid-column: 4 / 5;
        grid-row: 1 / 2;
    }

    &-4 {
        grid-column: 5 / 6;
        grid-row: 1 / 2;
    }

    &-5 {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
    }

    &-6 {
        grid-column: 3 / 4;
        grid-row: 2 / 3;
    }

    &-7 {
        grid-column: 4 / 5;
        grid-row: 2 / 3;
    }

    &-8 {
        grid-column: 5 / 6;
        grid-row: 2 / 3;
    }

    &-9 {
        grid-column: 2 / 3;
        grid-row: 3 / 4;
    }

    &-10 {
        grid-column: 3 / 4;
        grid-row: 3 / 4;
    }

    &-11 {
        grid-column: 4 / 5;
        grid-row: 3 / 4;
    }

    &-12 {
        grid-column: 5 / 6;
        grid-row: 3 / 4;
    }

    &-13 {
        grid-column: 2 / 3;
        grid-row: 4 / 5;
    }

    &-14 {
        grid-column: 3 / 4;
        grid-row: 4 / 5;
    }

    &-15 {
        grid-column: 4 / 5;
        grid-row: 4 / 5;
    }

    &-16 {
        grid-column: 5 / 6;
        grid-row: 4 / 5;
    }
}

.icon {
    height: 5rem;
    width: 5rem;

    @media only screen and (max-width: 900px) {
        height: 4rem;
        width: 4rem; 
    }

    @media only screen and (max-width: 900px) {
        height: 3rem;
        width: 3rem; 
    }

    @media only screen and (max-width: 500px) {
        height: 2rem;
        width: 2rem; 
    }
}

.card-container {
    opacity: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: all 1s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-id {
    font-size: 0;
}