.dashboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 5rem;
    padding: 4rem 2rem;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(255,255,255,1) 97%, rgba(255,255,255,1) 100%);    
}

.dashboard h1 {
    font-size: 2rem;
    color: white;
}

.start {
    font-size: 1.5rem;
    padding: 1rem;
    outline: none;
    border: none;
    border-radius: .5rem;
    background-color: rgb(126, 204, 0);
}

.timer {
    font-size: 1.6rem;
}

.moves {
    font-size: 1.6rem;
}